import cuRate from "@/colorui/components/cu-rate.vue";
export default {
  components: { cuRate },
  /*
  初始化数据
  */
  data() {
    return {
      tagList: ["味道不错", "肉多", "辣椒很辣", "分量足"],
      myTagList: [],
      imgList: []
    };
  },
  computed: {
    // 计算属性的 getter
  },
  onLoad: function(option) {
    console.log(option);
  },
  onShow: function() {
    console.log("pages/order/evaluate");
  },
  methods: {
    _removeTag(value) {
      let myTagList = this.myTagList;
      let index = myTagList.indexOf(value);
      if (index != -1) {
        myTagList.splice(index, 1);
      }
    },
    _addTag(value) {
      this.myTagList.push(value);
    },
    ChooseImage() {
      uni.chooseImage({
        count: 4, //默认9
        sizeType: ["original", "compressed"], //可以指定是原图还是压缩图，默认二者都有
        sourceType: ["album"], //从相册选择
        success: res => {
          if (this.imgList.length != 0) {
            this.imgList = this.imgList.concat(res.tempFilePaths);
          } else {
            this.imgList = res.tempFilePaths;
          }
        }
      });
    },
    ViewImage(e) {
      uni.previewImage({
        urls: this.imgList,
        current: e.currentTarget.dataset.url
      });
    },
    DelImg(e) {
      uni.showModal({
        title: "提示",
        content: "确定要删除这张照片吗？",
        cancelText: "取消",
        confirmText: "确定",
        success: res => {
          if (res.confirm) {
            this.imgList.splice(e.currentTarget.dataset.index, 1);
          }
        }
      });
    },
    getChangeA(data) {
      console.log(data);
    },
    getChangeB(data) {
      console.log(data);
    },
    getChangeC(data) {
      console.log(data);
    }
  }
};
